.aboutContainer {
  justify-content: center;
  text-align: center;
  margin-left: 25vw;
}

.aboutText {
  margin-top:4vh;
  height: 80%;
  width: 50vw;
  color: #21255a;
  font-size: 2.5vmin;
}

h1 {
  color: #fff;
  text-align: center;
  text-decoration: underline;
  font-size: max(20px,7vmin);
}

.header {
  margin-top: -5vh;
  background-color: #21255a;
  height: max(40px,12vh);
}
.player-wrapper {
  display: block;
  margin: 0 auto;
  width:50vw;
  border: 40px solid black;
}

video {
  transform: translate(-50%);
  position: absolute;
  left: 50%;
}